.picker_main{
    width: 100vw;
    height: 8vh;
    margin-top: 2%;
}

.picker_bar{
    position:absolute;
    width: 40vw;
    height: 8vh;
    margin-left: 28%;
}

.picker_bar > form > select{
    width: 100%;
    height: 57px;
    box-sizing: border-box;
    padding-left: 15px;
    font-family:Arial, Helvetica, sans-serif;
    font-weight: 600;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    border-radius: 2px;
    outline: none;
    border: none;
    background-color:#212121;
    color: floralwhite;
}


@media screen and  (max-width: 596px){
    .picker_main{
        margin-top: 63%;
    }
    .picker_bar {
        width: 80%;
        margin-left: 20%;
    }
}


