.container{
    margin-top: 2%;
    width: 100%;
    height: 60vh;
    box-sizing: border-box;
}

.subcontainer{
    width: 900px;
    box-sizing: border-box;
    margin-left: 17%;
}

@media screen and  (max-width: 1065px){
    .subcontainer{
        width: 800px;
        margin-left: 10%;
    }   
}

@media screen and  (max-width: 916px){
    .subcontainer{
        width: 750px;
        margin-left: 7%;
    }   
}

@media screen and  (max-width: 819px){
    .subcontainer{
        width: 700px;
        margin-left: 5%;
    }   
}

@media screen and  (max-width: 596px){
    .subcontainer{
        width: 600px;
        margin-left: 0%;
        margin-top: 3%;
    }   
}


@media screen and  (max-width: 286px){
    .subcontainer{
        width: 600px;
        margin-left: 0%;
        margin-top: 20%;
    }   
}
