.main_title{
    display: flex;
    justify-content: center;
    align-items: center;
}
.titles_item{
    display: flex;
    justify-content: center;
    align-items: center;
}

.titles_item  p{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: cursive;
    font-size: 24px;
}

.titles_item > p > img{
    width: 44px;
}


@media screen and  (max-width: 446px){
    .titles_item{
        /* margin-left: 5%; */
        width: 100%;
    }
    
    .titles_item  p{
        font-size: 20px;
    }
    
    .titles_item > p > img{
        width: 40px;
    }
        
}

