.cards_main{
    width: 100vw;
    height: 22vh;
    margin-top: 1%;
}

.cards_1{
    position: relative;
    margin-left: 18%;
    width: 15%;
    height: 100%;
    display: inline-block;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.cards_2{
    position: relative;
    margin-left: 8%;
    width: 15%;
    height: 100%;
    display: inline-block;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.cards_3{
    position: relative;
    margin-left: 8%;
    margin-right: 20%;
    width: 15%;
    height: 100%;
    display: inline-block;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}
.cards_1_main, .cards_2_main, .cards_3_main{
    position: absolute;
    margin-left: 0%;
    width: 100%;
    height: 100%;
}

.cards_1_name, .cards_2_name, .cards_3_name{
    position: absolute;
    margin-left: 0%;
    width: 100%;
    height: 20%;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}
.cards_1_name p, .cards_2_name p, .cards_3_name p{
    display: flex;
    justify-content: left;
    align-items: flex-end;
    width: 95%;
    height: 100%;
    margin-top: 0%;
    padding-left: 5%;
    font-size: 1vw;
}

.cards_1_value, .cards_2_value, .cards_3_value{
    position: absolute;
    margin-left: 0%;
    top: 20%;
    width: 100%;
    height: 20%;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}

.cards_1_value p, .cards_2_value p, .cards_3_value p{
    display: flex;
    justify-content: left;
    align-items: flex-end;
    width: 95%;
    height: 100%;
    margin-top: 0%;
    padding-left: 5%;
    font-size: 1.5vw;
    font-weight: 600;
}
.cards_1_date, .cards_2_date, .cards_3_date{
    position: absolute;
    margin-left: 0%;
    top: 40%;
    width: 100%;
    height: 20%;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}

.cards_1_date p, .cards_2_date p, .cards_3_date p{
    display: flex;
    justify-content: left;
    align-items: flex-end;
    width: 95%;
    height: 100%;
    margin-top: 0%;
    padding-left: 5%;
    font-size: 1vw;
}
.cards_1_discription, .cards_2_discription, .cards_3_discription{
    position: absolute;
    margin-left: 0%;
    top: 60%;
    width: 100%;
    height: 20%;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    
}

.cards_1_discription p, .cards_2_discription p, .cards_3_discription p{
    display: flex;
    justify-content: left;
    align-items: flex-end;
    width: 95%;
    height: 100%;
    margin-top: 0%;
    padding-left: 5%;
    font-size: 0.75vw;
}


.cards_1_line, .cards_2_line, .cards_3_line{
    position: absolute;
    margin-left: 0%;
    top: 80%;
    width: 100%;
    height: 20%;
}

.cards_1_line p{
    position: absolute;
    top: 90%;
    width: 100%;
    height: 12%;
    margin: 0%;
    padding: 0%;
    background-color: darkturquoise;
}

.cards_2_line p{
    position: absolute;
    top: 90%;
    width: 100%;
    height: 12%;
    margin: 0%;
    padding: 0%;
    background-color:lawngreen;
}


.cards_3_line p{
    position: absolute;
    top: 90%;
    width: 100%;
    height: 12%;
    margin: 0%;
    padding: 0%;
    background-color:tomato;
}


